import ssPaymentPassword from '../../components/sanshui-payment-password'
export default {
	components: {
		ssPaymentPassword
	},
	data() {
		return {
			balance: 0,
			isSubmit: false, //是否提交中
			price: ''
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		this.getUserAccont();
	},
	methods: {
		getUserAccont() {
			//获取账户
			var _this = this;
			_this.$request.user
				.getUserData({})
				.then(data => {
					if (data.status == 1) {
						if (_this.type == 2) {
							_this.balance = data.data.balance;
						} else {
							_this.balance = data.data.acer_truncatum;
						}
					} else {
						_this.$tools.toast(data.msg);
						
					}
				})
				.catch(err => {
					//消息异常
					_this.$tools.toast('数据加载异常');
				});

		},
		openPwd() {
			var _this = this;
			if (_this.price == '') {
				_this.$tools.toast('请输入正确的提现金额');
				return false;
			}
			if (parseFloat(_this.price) > _this.balance) {
				_this.$tools.toast('账户余额不足');
				return false;
			}
			//验证密码
			this.$refs.paymentPassword.modalFun('show');
		},
		withdrawal(e) {
			//提现
			var _this = this;
			if (_this.isSubmit) return false;
			if (_this.price == '') {
				_this.$tools.toast('请输入正确的金额');
				return false;
			}
			if (parseFloat(_this.price) > _this.balance) {
				_this.$tools.toast('账户余额不足');
				return false;
			}
			_this.isSubmit = true;
			_this.$tools.loading('数据提交中');
			_this.$request.account
				.dChon({
					money: _this.price,
					pwdPay: e.value
				})
				.then(data => {
					_this.$tools.loadingHide();
					_this.isSubmit = false;
					if (data.status == 1) {
						_this.$tools.toast('申请成功');
						setTimeout(() => {
							uni.navigateBack();
						}, 1000)
					} else {
						_this.$refs.paymentPassword.modalFun('hide');
						_this.$tools.toast(data.msg);
					}
				})
				.catch(err => {
					_this.$refs.paymentPassword.modalFun('hide');
					_this.isSubmit = false;
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常');
				});
		}
	}
};
